html {
  position: fixed;
  height: 100%;
  overflow: hidden;
  overscroll-behavior: none;
  -ms-scroll-chaining: none;
}

body {
  font-family: sans-serif;
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: none;
  -ms-scroll-chaining: none;
  background-color: blanchedalmond
}

.container {
  position: fixed;
  height:100vh;
  width:100vw;
  overflow:auto;
  touch-action: pan-y;
  overscroll-behavior: none;
  -ms-scroll-chaining: none;
}

.list-item {
  width:100%;
  height:50px;
  margin: 2px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: grey;
}

.drag-style {
  background-color: orange;
}